body.rdi__fade {
    div.rdi__menu {
        width: 100vw;
        height: 100vh;
        position: absolute;
        z-index: 5000;
        background: rgba(var(--rdi-menu-bg), 1);
        opacity: 0;
        will-change: opacity;
        transition: opacity 0.25s ease-out;
        &>* {
            will-change: transform;
            transform: scale(0.01);
            transition: transform 0.5s ease-out; } }
    div.rdi__page {
        width: 100vw;
        min-height: 100vh;
        position: absolute;
        z-index: 0;
        background: url('../assets/hub1.webp') center center/cover no-repeat;
        .no-webp & {
            background: url('../assets/hub1.jpg') center center/cover no-repeat; } }
    &.rdi__show {
        overflow: hidden;
        div.rdi__menu {
            opacity: 1;
            &>* {
                transform: scale(1);
                transition: transform 0.25s ease-in; } } } }
