//\:root
 :root {
    @include font-p;
    @include text-shadow;
    color: $white;
    @include fluid-type($min-screen, $max-screen, $min-font, $max-font);
    text-rendering: optimizeLegibility;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    --rdi-menu-bg: 34,34,34; }


::selection {
    background-color: $secondary; }

body {
    background-color: $primary;

    h1, h2, h3, h4, h5, h6 {
        @include font-h;
        margin: 0;
        box-sizing: border-box;
        letter-spacing: 0.05rem; }

    a {
        text-decoration: none;
        color: $white;
        // STOP SAFARI LINK BLINKING
        -webkit-tap-highlight-color: transparent; }

    i {
        speak: none;
        font-weight: normal;
        font-style: normal; }


    /* PAGE WRAPPERS */
    i.rdi__menu-btn {
        margin: 0;
        color: $white;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 9000;
        cursor: pointer;
        font-size: 1.5rem;
        padding: 8px;
        span.close {
            display: none; } }
    &.rdi__show {
        i.rdi__menu-btn {
            span.open {
                display: none; }
            span.close {
                display: block; } } }

    div.rdi__menu {
        display: flex;
        flex-direction: column;
        align-items: center;
        &>header {
            flex-grow: 1;
            display: flex;
            align-items: center;
            h1 {
                text-transform: uppercase; } }
        &>nav {
            flex-grow: 3;
            transform-origin: top;
            ul {
                margin: 0;
                padding: 0;
                list-style: none; } } } }


