body.rdi__slip {
    div.rdi__menu, div.rdi__page {
        transition: transform 0.5s ease-in;
        will-change: transform; }
    div.rdi__menu {
        width: 90vw;
        height: 100vh;
        position: absolute;
        z-index: 5000;
        transform: translateX(-100%);
        background: rgba(var(--rdi-menu-bg), 1); }
    div.rdi__page {
        width: 100vw;
        min-height: 100vh;
        position: absolute;
        z-index: 0;
        background: url('../assets/hub3.webp') center center/cover no-repeat;
        .no-webp & {
            background: url('../assets/hub3.jpg') center center/cover no-repeat; } }
    &.rdi__show {
        overflow: hidden;
        div.rdi__menu, div.rdi__page {
            transition: transform 0.5s ease-out; }
        div.rdi__menu {
            box-shadow: 0 1px 3px rgba($black, 0.12), 0 1px 2px rgba($black, 0.24);
            transform: translateX(0%); }
        div.rdi__page {
            transform: translateX(50%); } } }
