body.rdi__slide {
    div.rdi__menu {
        transition: transform 0.5s ease-in;
        will-change: transform; }
    div.rdi__menu {
        width: 90vw;
        height: 100vh;
        position: absolute;
        z-index: 5000;
        transform: translateX(-100%);
        background: rgba(var(--rdi-menu-bg), 0.85); }
    div.rdi__page {
        width: 100vw;
        min-height: 100vh;
        position: absolute;
        z-index: 0;
        background: url('../assets/hub2.webp') center center/cover no-repeat;
        .no-webp & {
            background: url('../assets/hub2.jpg') center center/cover no-repeat; } }
    &.rdi__show {
        overflow: hidden;
        div.rdi__menu {
            transform: translateX(0%);
            transition: transform 0.5s ease-out; } } }
