body.home {
    --max-width: 1200px;
    --item-width: 15vw;
    header {
        height: 15vh;
        display: flex;
        justify-content: space-around;
        align-items: center;
        h1 {
            color: $black; } }
    nav {
        ul {
            margin: 0 auto;
            padding: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            max-width: var(--max-width);
            li {
                list-style: none;
                width: var(--item-width);
                margin: 0 1rem;
                border: 1px solid $black;
                display: flex;
                justify-content: center;
                align-items: center;
                background-color: $secondary;
                a {
                    display: block;
                    padding: 1rem 0;
                    width: 100%;
                    text-align: center;
                    text-transform: capitalize; } } } } }
